/* global angular */
import PluralizeFilter from './pluralize-filter';

const moduleKey = 'ui-wm-pluralize-filter-module';

export default angular
  .module(moduleKey, [])
  .filter('i18nPluralize', [
    PluralizeFilter,
  ])
  .name;



